import moment from 'moment'
const filters = {
    status(value) {
        if (value === 0) return " "
        if (value === 10) return ""
        return " "
    },
    genderType(value) {
        switch (value) {
            case 'f':
                return ""
            case 'm':
                return ""
            case "ch":
                return ""
        }
    },
    percentable (val) {
        if(!val) return ''
        return  val + '%'
    },
    orderStatus(value) {
        switch (value) {
            case 0:
                return ""
            case 1:
                return " "
            case 2:
                return " "
            case 3:
                return "  "
            case 4:
                return "  "
            case 10:
                return ""
        }
    },
    cardType(value) {
        switch (value) {
            case 'cash':
                return  value
            case 'card':
                return  value
            case 'self_card':
                return  value
            default:
                return value
        }
    },
    money (val) {
        return val ? val.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ') : 0
    },
    ifNone(value) {
        if (!value) return " "
        return value
    },
    formatterDate(value) {
        return moment(value).format('YYYY-MM-DD')
    },
    formatterFullTime(value) {
        return moment(value).format('YYYY-MM-DD HH:MM')
    },
    formatterPhone(value) {
        if(!value) return  ''
        return  value.replace(/(\d{2})(\d{3})(\d{2})(\d{2})/g, '$1 $2 $3 $4')
    }
}

export default {
    install(Vue) {
        Object.keys(filters).forEach(filter => {
            Vue.filter(filter, filters[filter])
        })
    }
}
